@import "./colors.scss";

blockquote,body,dd,dl,dt,fieldset,figure,h1,h2,h3,h4,h5,h6,hr,html,iframe,legend,li,ol,p,pre,textarea,ul{margin:0;padding:0}h1,h2,h3,h4,h5,h6{font-size:100%;font-weight:400}ul{list-style:none}button,input,select,textarea{margin:0}html{box-sizing:border-box}*,::after,::before{box-sizing:inherit}img,video{height:auto;max-width:100%}iframe{border:0}table{border-collapse:collapse;border-spacing:0}td,th{padding:0}td:not([align]),th:not([align]){text-align:left}

html {
  font-size: 64px;
}

.App {
  position: relative;
  overflow: hidden;
  /*border: 1px solid $black;*/
}

.placeholder {
  width: 100%;
  height: 100%;
  position: absolute;
  z-index: 1;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  opacity: 0.3;
  background-size: contain;
}

.subheadline {
  font-size: 1rem;
}

h1,
.call-to-action {
  font-family: "Neue Plak Text";
  color: $white;
  font-weight: bold;
  line-height: 1;
}

h2 {
  font-family: "Neue Plak Text Semi Bold";
  text-transform: uppercase;
  color: $dark-grey;
  font-weight: 400;
  line-height: 1;
}

h3 {
  font-family: "Cloudera Display 100 Striped";
  font-weight: normal;
  line-height: 1;
  color: $dark-orange;
}

.text-area {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  /*background: beige;*/
  word-break: break-word;
}

.subheadline{
  width: 100%;
}

.call-to-action {
  border-radius: 1rem;
}

.call-to-action p {
  text-align: center;
  text-transform: uppercase;
}

.display-line{
  text-transform: uppercase;
}

